'use client';
import styles from '../cart/cart.module.css';

interface CartItem {
    id: string;
    name: string;
    price: number;
    image: string;
    quantity: number;
}

interface CartListProps {
    items: CartItem[];
    onIncrease: (id: string) => void;
    onDecrease: (id: string) => void;
    onRemove: (id: string) => void;
}

export default function CartList({ items, onIncrease, onDecrease, onRemove }: CartListProps) {
    if (items.length === 0) {
        return <p>Gi·ªè h√†ng tr·ªëng</p>;
    }

    return (
        <div className={styles.cartList}>
            {items.map((item) => (
                <div key={item.id} className={styles.cartItem}>
                    <img
                        src={item.image}
                        alt={item.name}
                        className={styles.itemImage}
                        onError={(e) => {
                            const target = e.target as HTMLImageElement;
                            target.src = '/fallback.jpg';
                            target.alt = 'H√¨nh ·∫£nh kh√¥ng kh·∫£ d·ª•ng';
                        }}
                    />

                    <div className={styles.itemInfo}>
                        <h3>{item.name}</h3>
                        <p>Gi√°: <strong>{item.price.toLocaleString('vi-VN')} VNƒê</strong></p>
                        <p>S·ªë l∆∞·ª£ng: <strong>{item.quantity}</strong></p>
                        <p>T·ªïng: <strong>{(item.price * item.quantity).toLocaleString('vi-VN')} VNƒê</strong></p>
                    </div>

                    <div className={styles.itemActions}>
                        <div className={styles.quantityControl}>
                            <button onClick={() => onDecrease(item.id)}>-</button>
                            <span>{item.quantity}</span>
                            <button onClick={() => onIncrease(item.id)}>+</button>
                        </div>
                        <button className={styles.removeBtn} onClick={() => onRemove(item.id)}>X</button>
                    </div>
                </div>
            ))}
        </div>
    );
}
